import { record } from 'rrweb';
import { eventWithTime, listenerHandler } from 'rrweb/typings/types.d';
import io from 'socket.io-client';
import omit from 'omit.js';

export interface IOptions {
  token: string;
  blockClass?: string | RegExp;
  ignoreClass?: string;
  maskAllInputs?: boolean;
}
export type userType = 'user' | 'session';

export interface IRepData {
  code: number;
  body: {
    [key: string]: any;
  };
  message: string;
}
export interface ISocketOptions {
  token: string;
  userId: string;
  sessionId: string;
}

export interface IVerifyTokenBody {
  token: string;
  userId: string;
  sessionId: string;
  pageUrl: string;
  screenWidth: number;
  screenHeight: number;
  resolution: string;
}

export interface IUpdateDocumentVisibilityStateBody {
  sessionId: string;
  visibilityState: boolean;
  isLive: boolean;
}

export default class TrackSession {
  private options: IOptions;
  private socket: SocketIOClient.Socket;
  private socketLive: SocketIOClient.Socket;
  private sessionId: string;
  private userId: string;
  // private domain: string = 'https://tracksession.rebornautofinance.com';
  private domain: string = 'http://127.0.0.1:7008';

  private recordNSP: string = 'record';
  private liveNSP: string = 'live';
  private storeKey: string = 'tracksession-';
  private recordStopFn: listenerHandler | undefined;
  private liveStopFn: listenerHandler | undefined;
  private static isCertified = false;
  // private recordList: eventWithTime[] = [];
  constructor(options: IOptions) {
    this.options = options;
    this.init();
  }
  public static addCustomEvent(tag: string, paylaod: unknown) {
    if (TrackSession.isCertified && record) {
      record.addCustomEvent(tag, paylaod);
    }
  }
  private init() {
    this.verifyToken();
  }
  private async request<T>(url: string, body: T) {
    const response = await fetch(`${this.domain}${url}`, {
      body: JSON.stringify(body),
      headers: {
        'Content-Type': 'application/json',
      },
      method: 'POST',
    });
    const json: IRepData = await response.json();
    return json;
  }
  private async verifyToken() {
    const { token = '' } = this.options;
    if (!token) throw new Error('token is required');
    const pageUrl = window.location.href;
    const screenWidth = window.innerWidth;
    const screenHeight = window.innerHeight;
    const resolution = `${window.screen.width}*${window.screen.height}`;
    const userId = this.getUserByType('user');
    const sessionId = this.getUserByType('session');
    const bodyParams: IVerifyTokenBody = {
      token,
      userId,
      sessionId,
      pageUrl,
      screenWidth,
      screenHeight,
      resolution,
    };
    const json = await this.request('/verifytoken', bodyParams);
    const { code, body = {} } = json;
    if (code === 200) {
      const { userId, sessionId } = body;
      TrackSession.isCertified = true;
      this.sessionId = sessionId;
      this.userId = userId;
      this.setUserByType('user', userId);
      this.setUserByType('session', sessionId);
      this.openSocket();
      this.onDocumentVisibilityState();
      // console.log(true)
    }
  }
  private async onDocumentVisibilityState() {
    const updateHandle = async (visibilityState: boolean) => {
      const sessionId = this.getUserByType('session');
      const bodyParams: IUpdateDocumentVisibilityStateBody = {
        sessionId,
        visibilityState,
        isLive: true,
      };
      await this.request('/updatevisibilitystate', bodyParams);
    };

    document.addEventListener('visibilitychange', async () => {
      // console.log(document.visibilityState);
      const visibilityState = document.visibilityState === 'visible';
      updateHandle(visibilityState);
    });
    window.addEventListener('beforeunload', async (event) => {
      event.preventDefault();
      // console.log('beforeunload');
      updateHandle(false);
    });
  }
  private getUserByType(type: userType): string {
    const { token } = this.options;
    try {
      const key = `${this.storeKey}-${type}id-${token}`;
      if (type === 'session') {
        return sessionStorage.getItem(key) || '';
      } else {
        return localStorage.getItem(key) || '';
      }
    } catch (error) {
      throw new Error('浏览器版本不支持');
    }
  }
  private setUserByType(type: userType, userid: string) {
    const { token } = this.options;
    try {
      const key = `${this.storeKey}-${type}id-${token}`;
      if (type === 'session') {
        sessionStorage.setItem(key, userid);
      } else {
        localStorage.setItem(key, userid);
      }
    } catch (error) {
      throw new Error('浏览器版本不支持');
    }
  }
  private initSocketEvent() {
    this.socket.on('connect', () => {
      console.log('connect*****');
      this.startRecord();
    });
    this.socket.on('reconnect', () => {
      this.restartRecord();
    });
    this.socket.on('connect_error', (error: object) => {
      // console.log('connect_error', error);
    });
    this.socket.on('connect_timeout', (timeout: number) => {
      // console.log('connect_timeout', timeout);
    });
    this.socket.on('disconnect', (reason: string) => {
      console.log('disconnect', reason);
      this.stopRecord();
      if (reason === 'io server disconnect') {
        this.socket.connect();
      }
    });
    this.socket.on('online', (msg: any) => {
      console.log('msg', msg);
      this.restartRecord();
    });
  }
  private initLiveSocketEvent() {
    this.socketLive.on('connect', () => {
      console.log('connect*****');
      this.startLiveRecord();
    });
    this.socketLive.on('reconnect', () => {
      this.restartLiveRecord();
    });
    this.socketLive.on('connect_error', (error: object) => {
      console.log('connect_error', error);
    });
    this.socketLive.on('connect_timeout', (timeout: number) => {
      console.log('connect_timeout', timeout);
    });
    this.socketLive.on('disconnect', (reason: string) => {
      console.log('disconnect', reason);
      this.stopRecord();
      if (reason === 'io server disconnect') {
        this.socketLive.connect();
      }
    });
    this.socketLive.on('online', (msg: any) => {
      console.log('msg', msg);
      this.restartLiveRecord();
    });
  }
  private openSocket() {
    const { token } = this.options;
    const url = `${this.domain}/${this.recordNSP}`;
    const liveUrl = `${this.domain}/${this.liveNSP}`;
    const option: ISocketOptions & { room: string } = {
      token,
      sessionId: this.sessionId,
      userId: this.userId,
      room: this.sessionId,
    };
    const liveOption: ISocketOptions & { room: string } = {
      token,
      sessionId: this.sessionId,
      userId: this.userId,
      room: this.sessionId,
    };
    this.socket = io(url, {
      query: option,
      transports: ['websocket'],
      path: '/sessiontrack',
    });
    // this.socketLive = io(liveUrl, {
    //   query: liveOption,
    //   transports: ['websocket'],
    //   forceNew: true,
    //   path: '/sessiontrack',
    // });

    this.initSocketEvent();
    // this.initLiveSocketEvent();
  }
  private startRecord() {
    const recordOptions: Pick<
      IOptions,
      'blockClass' | 'ignoreClass' | 'maskAllInputs'
    > = omit(this.options, ['token']);

    this.recordStopFn = record({
      emit: (event: eventWithTime) => {
        console.log('record', event);
        this.socket.emit(this.recordNSP, event);
      },
      ...recordOptions,
    });
  }
  private stopRecord() {
    if (this.recordStopFn) {
      this.recordStopFn();
    }
  }
  private startLiveRecord() {
    const recordOptions: Pick<
      IOptions,
      'blockClass' | 'ignoreClass' | 'maskAllInputs'
    > = omit(this.options, ['token']);
    this.liveStopFn = record({
      emit: (event: eventWithTime) => {
        console.log('live', event);
        this.socketLive.emit(this.liveNSP, event);
      },
      ...recordOptions,
    });
  }
  private stopLiveRecord() {
    if (this.liveStopFn) {
      this.liveStopFn();
    }
  }
  private restartLiveRecord() {
    this.stopLiveRecord();
    this.startLiveRecord();
  }
  private restartRecord() {
    this.stopRecord();
    this.startRecord();
  }
}
